html, body {
    width: 100%;
    min-height: 100vh;
    display: flex;
    margin: 0;
    size: 16px;
}

div, aside, section, main {
    box-sizing: border-box;
}

.description {
    display: flex;
    flex-direction: column;
    padding: 2% 6%;
    width: 50%;
    color: #FEFFFF;
    background-color: #2B7A78;
}

.description__header {
    font-family: Gotham SSm A,Gotham SSm B,Helvetica,Arial,sans-serif;
    margin-bottom: 5px;
    font-size: 3.1em;
    text-transform: uppercase;
}

.description__info {
    font-family: sans-serif;
    line-height: 1.3;
    font-size: 1.4em;
    align-self: baseline;
}
.description__info a {
    color: #FEFFFF;
}

.content {
    display: flex;
    width: 50em;
    flex-grow: 1;
    height: 100%;
    background-color: #3AAFA9;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1em;
}

.clock {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20em;
    padding: .5em 1em;
}

.clock__seconds {
    width: 5em;
    height: 5em;
    border-radius: 50%;
    border: .6em solid #2B7A78;
    background-color: #17252A;
    margin-bottom: .5em;
    position: relative;
    transition: background-color .4s ease-in-out;
}

.clock__seconds:after {
    content: "";
    height: 1.1em;
    position: absolute;
    width: 0.7em;
    bottom: -1.2em;
    left: 1.5em;
    background-color: #2B7A78;
}

.clock__row {
    width: 100%;
    height: 3.8em;
    display: flex;
    justify-content: space-between;
    padding: .6em 1em;
    align-items: center;
    margin-bottom: .5em;
    border-radius: 1em;
    background-color: #2B7A78;
    position: relative;
}

.clock__row:after {
    content: "";
    height: 1.1em;
    position: absolute;
    width: 50%;
    bottom: -1em;
    left: 4em;
    border-left: 0.7em solid #2B7A78;
    border-right: 0.7em solid #2B7A78;
}

.clock__hours,
.clock__minutes-additional {
    height: 100%;
    border-radius: 0.3em;
    width: 3.4em;
    background-color: #17252A;
}

.clock__minutes {
    height: 2.8em;
    border-radius: 0.3em;
    width: 1em;
    background-color: #17252A;
}

.time {
    border: .2em solid #2B7A78;
    width: 100%;
    min-height: 2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #2B7A78;
    font-family: Gotham SSm A,Gotham SSm B,Helvetica,Arial,sans-serif;
    font-size: 1.8em;
    color: #17252A;
    transition: opacity .5s ease-in-out;
    user-select: none;
}

.time__timezone {
    font-size: .7em;
}
